import React, { Component } from 'react';
// import logo from './logo.svg';
import { Button, Menu, Icon, Card } from 'antd';
import './styles/index.less'

const {Meta} = Card

class App extends Component {
  render() {
    return (
      <div className="App">
        <header className="p-t-l">
          <h1 className="text-center"></h1>
        </header>
        <Menu
          selectedKeys={['/']}
          mode='horizontal'
        >
          <Menu.Item key='/'>
            <Icon type='mail' /> HOME
          </Menu.Item>
          <Menu.Item key='/test'>
            <Icon type='appstore' /> test
          </Menu.Item>
          <Menu.Item key='/news'>
            <Icon type='smile-o' /> NEWS
          </Menu.Item>
        </Menu>
        <ul className='m-v-xl'>
          <li>antd3.8.4</li>
          <li>background, grid, layout, space, textsrc/styles/*.less</li>
          <li>build/static/css/main.xxxx.css<span className='text-primary'>hash</span> </li>
          <li>src/styles/vars.lessantd</li>
          <li>less</li>
        </ul>
        <ol>
          <li>
            <h4>Button</h4>
            <div>
              <Button type="primary">Button</Button>
              <Button className='ant-btn-xl ant-btn-success'>Button</Button>
              <Button className='ant-btn-xl ant-btn-info' ghost>Button</Button>
              <Button size='sm'>Button</Button>
            </div>
          </li>
          <li>
            <h4>Card</h4>
            <div>
            <Card
              hoverable
              style={{ width: 240 }}
              cover={<img alt="example" src="https://os.alipayobjects.com/rmsportal/QBnOOoLaAfKPirc.png" />}
            >
              <Meta
                title="Europe Street beat"
                description="www.instagram.com"
              />
            </Card>
            </div>
          </li>
        </ol>
      </div>
    );
  }
}

export default App;



